/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import { useDispatch } from 'react-redux';
import { motion } from 'framer-motion';
import { React, useState } from 'react';
import { deleteBook } from '../../redux/books/bookSlice';
import ProgressCircle from '../progressCircle/ProgressCircle';
import Chapter from '../chapter/Chapter';
import styles from './bookStyle.module.scss';

const containerA = {
  initial: {},
  animate: {
    opacity: 1,
    transition: {
      staggerChildren: 0.2,
      delayChildren: 0.5,
    },
  },
  exit: {
    scale: 0,
    opacity: 0,
  },
};

const containerB = {
  initial: {},
  animate: {
    transition: {
      staggerChildren: 0.2,
      delayChildren: 0.8,
    },
  },
};

const childVariantA = {
  initial: {
    x: 50,
    opacity: 0,
  },
  animate: {
    x: 0,
    opacity: 1,
    transition: {
      type: 'spring',
      damping: 10,
      mass: 0.75,
      stiffness: 100,
    },
  },
};

const childVariantB = {
  initial: {
    scale: 0,
    opacity: 0,
  },
  animate: {
    scale: 1,
    opacity: 1,
    transition: {
      type: 'spring',
      damping: 10,
      mass: 0.75,
      stiffness: 200,
    },
  },
};

const Book = (props) => {
  const dispatch = useDispatch();
  const [percent, setPercent] = useState(0);
  const {
    id, title, category, variants,
  } = props;
  const handleRemove = () => {
    dispatch(deleteBook({ id }));
  };

  const changeHandler = (event) => {
    setPercent(event.target.value / 100);
  };

  return (
    <motion.li variants={variants}>
      <input onChange={changeHandler} type="text" name="value" placeholder="test percent" />
      <motion.div
        className={styles.mainContainer}
        variants={containerA}
        initial="initial"
        animate="animate"
        exit="exit"
      >
        <motion.div
          variants={childVariantA}
          className={styles.dataContainer}
        >
          <p className={styles.category}>
            {category}
          </p>
          <p className={styles.title}>
            {title}
          </p>
          <p className={styles.author}>
            author
          </p>
          <motion.div
            variants={containerB}
            className={styles.optionsContainer}
          >
            <motion.div
              variants={childVariantB}
            >
              <motion.button
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                className={styles.btn}
                type="button"
              >
                Comments
              </motion.button>
            </motion.div>
            <motion.div
              variants={childVariantB}
            >
              <motion.button
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                className={styles.btn}
                onClick={handleRemove}
                type="motion.button"
              >
                Remove
              </motion.button>
            </motion.div>
            <motion.div
              variants={childVariantB}
            >
              <motion.button
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                className={styles.btn}
                type="button"
              >
                Edit
              </motion.button>
            </motion.div>
          </motion.div>
        </motion.div>
        <div className={styles.progressContainer}>
          <div className={styles.circlePercent}>
            <ProgressCircle
              percent={percent}
            />
          </div>
        </div>
        <Chapter />
      </motion.div>
    </motion.li>
  );
};
export default Book;
